import Vue from 'vue';
import Router from 'vue-router';
import Index from '../page/index';
import Home from '../page/home';
import Air from '../page/air';
import Bus from '../page/bus';
import Dolphin from '../page/dolphin';
import Near from '../page/near';
import DonotPass from '../page/donotPass';
import Form from '../page/form';

Vue.use(Router);

const routes = [
    {
        path: '*',
        name: 'Index',
        component: Index,
    },
    {
        path: '/form',
        name: 'Form',
        component: Form,
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
    },
    {
        path: '/air',
        name: 'Air',
        component: Air,
    },
    {
        path: '/bus',
        name: 'Bus',
        component: Bus,
    },
    {
        path: '/dolphin',
        name: 'Dolphin',
        component: Dolphin,
    },
    {
        path: '/near',
        name: 'Near',
        component: Near,
    },
    {
        path: '/donotPass',
        name: 'DonotPass',
        component: DonotPass,
    }
];
const router = new Router({
    routes,
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }
        return { x: 0, y: 0 };
    },
    mode: 'hash',
});

// 设置路由标题
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next();
});

export default router;
