<template>
<div>
    <Form v-if="!isSubmitSuccess" @submit="onSubmit">
        <Field
            v-model="formValue.name"
            name="真实姓名"
            label="真实姓名"
            placeholder="真实姓名"
            :rules="[{ required: true, message: '请填写真实姓名' }]"
          />
        <Field
         v-model="formValue.spell"
         name="拼音"
         label="拼音"
         placeholder="拼音"
         :rules="[{ required: true, message: '请填写用户名拼音' }]"
        />
        <Field
         v-model="formValue.cardNum"
          name="护照号"
         label="护照号"
         placeholder="护照号"
         :rules="[{ required: true, message: '请填写护照号' }]"
        />
        <Field name="radio" label="性别">
          <template #input>
            <RadioGroup class="flex" v-model="formValue.sex" direction="horizontal">
              <Radio name="男">男&nbsp;&nbsp;&nbsp;</Radio>
              <Radio name="女">女</Radio>
            </RadioGroup>
          </template>
        </Field>
        <Field
          readonly
          clickable
          name="dateTimePicker"
          :value="formValue.born?dayjs(formValue.born).format('YYYY-MM-DD'): ''"
          label="出生日期"
          placeholder="点击选择出生日期"
          @click.stop="showDatePicker = true"
          :rules="[{ required: true, message: '请填写出生日期' }]"
        />
        <Popup v-model="showDatePicker" position="bottom">
          <DatetimePicker
              type="date"
              @confirm="confirmDateBorn"
              @cancel="showDatePicker = false"
              :min-date="new Date(1960,1,1)"
              :max-date="new Date()"
            />
        </Popup>
        <Field
         v-model="formValue.phone"
         name="phone"
         label="手机号"
         placeholder="手机号"
         :rules="[{ required: true, message: '请填写手机号' }]"
        />
        <Field
         v-model="formValue.email"
         name="email"
         label="邮箱"
         placeholder="邮箱"
         :rules="[{ required: true, message: '请填写邮箱' }]"
        />
        <Field
         v-model="formValue.wechat"
         name="formValue.wechat"
         label="微信号"
         placeholder="微信号"
         :rules="[{ required: true, message: '请填写微信号' }]"
        />
        <Field
          readonly
          clickable
          name="formValue.classDay"
          :value="formValue.classDay?dayjs(formValue.classDay).format('YYYY-MM-DD'): ''"
          label="到店日期"
          placeholder="点击选择到店日期"
          @click="showComeOnDate = true"
          :rules="[{ required: true, message: '请填写到店日期' }]"
        />
        <Popup v-model="showComeOnDate" position="bottom" >
          <DatetimePicker
            type="date"
            :min-date="new Date()"
            @cancel="showComeOnDate = false"
            @confirm="confirmComeonDate"
          />
        </Popup>
        <Field
         v-model="formValue.airNum"
         name="formValue.airNum"
         label="航班号"
         placeholder="航班号"
         :rules="[{ required: true, message: '请填写航班号' }]"
        />
        <Button style="margin: 20px auto 0; width: 270px" round block type="info" native-type="submit">
            提交
        </Button>
    </Form>
    <div v-else class="tip flex flex-dc flex-jc flex-ac">
        <div class="title-tip mt-30 mb-30">请确认信息正确无误</div>
        <div>
            {{Object.values(all).join(' \n')}}
        </div>

        <div class="confirm-tip txt-ac mt-30 mb-30">无误后请点击导出表格，</br>导出成功后表格和护照照片发送给氧小程</div>
        <JsonExcel
          class="export-btn flex-cc mt-30"
          :data="tableData"
          :fields="jsonFields"
          type="xls"
          header="氧程列表"
          name="氧程列表.xls"
          ref="excel"
        >
          <img src="../img/hand.png" class="hand"/>
          导出表格
        </JsonExcel>
    </div>
</div>
</template>
<script>
import dayjs from 'dayjs';
import JsonExcel from 'vue-json-excel'
import { Form, Field, RadioGroup, Radio, DatetimePicker, Popup, Button } from 'vant';
export default {
    name: 'airPage',
    components: {
        Form,
        Field,
        RadioGroup,
        Radio,
        DatetimePicker,
        Popup,
        Button,
        JsonExcel
    },
    data() {
        return {
            dayjs,
            isSubmitSuccess: false,
            formValue: {
                sex: '男',
                day: dayjs().format('YYYY/MM/DD'),
            },
            all: {},
            showDatePicker: false,
            showComeOnDate: false,
            tableData: [{type: 2, username: 'AAAAA'}],
            //导出Excel表格的表头设置
            jsonFields: {
              '中文名': 'name',
              '英文名': 'spell',
              '报名日期': 'day',
              '上课日期': 'classDay',
              '课程级别': 'courseLevel',
              '课程教练': 'instructor',
              '微信号': 'wechat',
              '出生日期': 'born',
              '手机号': 'phone',
              '邮箱': 'email',
              '所有信息': 'all',
          },
        };
    },
    computed: {},
    mounted() {},
    methods: {
        onSubmit() {
          this.isSubmitSuccess = true;
          this.all = {
            ...this.formValue,
             born: dayjs(this.formValue.born).format('YYYY/MM/DD'),
             classDay: dayjs(this.formValue.classDay).format('YYYY/MM/DD'),
          };
          console.log(this.all);
        },
        confirmDateBorn(value) {
            this.formValue.born = value;
            this.showDatePicker = false
        },
        confirmComeonDate(value) {
            this.formValue.classDay = value;
            this.showComeOnDate = false
        }
    },
};
</script>

<style lang="scss" scoped>
::v-deep.form {
    background: #fff;
}
.tip {
    font-size: 15px;
    margin: 0 auto;
}
.title-tip {
    font-size: 20px;
    font-weight: bold;
}
.confirm-tip {
    text-align: center;
}
.export-btn {
    font-family: PingFang sc;
    width: 120px;
    height: 40px;
    font-size: 20px;
    border: 1px solid #ef6158;
    color: #ef6158;
    border-radius: 5px;
    position: relative;
    .hand {
        width: 80px;
        height: 80px;
        position: absolute;
        right: 0px;
        top: 41px;
    }
}
</style>
